.src-atoms-button--button-stories-override-modules__root {
  border: none;
  padding: 1rem;
  background: gray;
  color: #fff;
  border-radius: 100px;
}

.src-atoms-button--button-stories-override-modules__small {
  font-size: smaller;
}

.src-atoms-button--button-stories-override-modules__large {
  font-size: larger;
}

.src-atoms-button--button-stories-override-modules__primary {
  background: blue;
}

.src-atoms-button--button-stories-override-modules__secondary {
  background: green;
}

.src-atoms-button--button-stories-override-modules__content {
  outline: 1px solid red;
}
